<template>
  <div class="comIng">
      <ul>
          <router-link v-for="movie in movies" :to="'/home/coming/'+movie.id" tag="li" :key="movie.id">
              <div class="poster">
                <img :src="movie.images.small" :alt="movie.title" />
              </div>
              <div class="movieInfo">
                  <h2>{{movie.title}}</h2>
                  <p>导演：{{movie.directors[0].name}}</p>
                  <p>主演：{{movie.casts[0].name}} ，{{movie.casts[1].name}}</p>
              </div>
          </router-link>
      </ul>
  </div>
</template>

<script>
    import star from './star/star'
    export default {
        name: 'comIng',
        data: function(){
            return {
                movies : []
            }
        },
        mounted () {
            this.$store.dispatch('showNav');
            this.$store.dispatch('showLoading');
            this.$http.jsonp('https://api.douban.com/v2/movie/coming_soon')
                .then((res)=>{
                    this.$store.dispatch('hideLoading');
                    this.movies = res.body.subjects;
                },(err)=>{
                    console.log(err);
                });
        },
        components: {
            star
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .comIng {
        height: 100%;
        overflow: auto;
        width: 100%;
        padding-top: 98px;
        padding-bottom: 40px;
    }
    .comIng li {
        display: flex;
        background: #f8f8f8;
        padding: 10px 20px;
        cursor: pointer;
        border-bottom: 1px solid #d6d6d6;
    }
    .comIng li p {
        font-size: 14px;
        color: #666;
    }
    .movieInfo {
        padding-left: 20px;
        vertical-align: top;
    }
    .movieInfo h2 {
        margin-top: 10px;
        font-size: 20px;
        font-weight: 500;
        margin-bottom: 20px;
    }

    img {
        margin-top: 7px;
    }
</style>


