<template>
  <div class="detail">
      <div class="top">
        <span @click="go(-1)"><</span>
        <h2>{{movieInfo.title}}</h2>
      </div>
      <div class="info">
        <div class="poster">
            <img :src="movieInfo.images.medium" alt="" /><!--这里有个bug！！！！！！！！！！！！！！！！-->
        </div>
        <div class="m-info">
            <h3>{{movieInfo.original_title}}</h3>
            <star :score="movieInfo.rating.average"></star>
            <p>{{movieInfo.rating.average}} ({{movieInfo.ratings_count}}人评分)</p>
            <p>{{movieInfo.year}} 年</p>
            <p><span v-for="item in genres">{{item}}  </span></p>
            <p>{{movieInfo.countries[0]}}</p>
            <p>{{movieInfo.durations[0]}}(中国大陆)</p>
            <p>{{movieInfo.mainland_pubdate}}(中国大陆/美国)</p>
        </div>
      </div>
      <div class="wtach">
        <span>{{movieInfo.wish_count}}人想看</span>
        <span>{{movieInfo.collect_count}}人看过</span>
      </div>
      <div class="desc">
        {{movieInfo.summary}}
      </div>
      <div class="stars">
        <p>演职人员</p>
        <ul>
            <router-link v-for="person in persons" :to="'/person/' + person.id" :key="person.id" tag="li">
                <img :src="person.avatars.small" alt=""/>
                <span>{{person.name}}</span>
            </router-link>
        </ul>
      </div>
      <div class="comments">
        <h3>热门短评<router-link :to="'/short/' + movieInfo.id" tag="p">查看全部短评>></router-link></h3>
        <ul>
            <li v-for="pop in pops">
                <div class="pop">
                    <div class="pop-left">
                        <img :src="pop.author.avatar" alt="" /><br>
                        <span>{{pop.author.name}}</span>
                    </div>
                    <div class="pop-right">
                        <star :score="pop.rating.value * 2"></star>
                        <span>{{pop.created_at}}</span>
                    </div>
                </div>
                <p>{{pop.content}}</p>
            </li>
        </ul>
      </div>
      <div class="foot">
        <router-link :to="'/detail/' + movieInfo.id" tag="p">查看全部影评>></router-link>
      </div>
  </div>
</template>

<script>
    import star from './star/star'
    export default {
        name: 'detail',
        data: function(){
            return {
                movieInfo : {},
                genres: [],
                persons: [],
                pops: []
            }
        },
        methods: {
            go: function(n){
                this.$router.go(n);
            }
        },
        mounted () {
            this.$store.dispatch('showNav');
            this.$store.dispatch('showLoading');
            this.$http.jsonp('https://api.douban.com/v2/movie/subject/' + this.$route.params.id + '?apikey=0b2bdeda43b5688921839c8ecb20399b&city=%E5%8C%97%E4%BA%AC&client=something&udid=dddddddddddddddddddddd')
                .then((res)=>{
                    this.$store.dispatch('hideNav');
                    this.$store.dispatch('hideLoading');
                    this.movieInfo = res.body;
                    this.genres = res.body.genres;
                    this.persons = res.body.casts;
                    this.pops = res.body.popular_comments;
                    // console.log(this.persons);
                    /*console.log(res.body);
                    console.log(res.body.images);
                    console.log(res.body.images.medium);*/
                },(err)=>{
                    console.log(err);
                });
            /*this.$http.jsonp('https://api.douban.com/v2/movie/subject/' + this.$route.params.id + '/reviews?apikey=0b2bdeda43b5688921839c8ecb20399b&start=0&count=20&client=something&udid=dddddddddddddddddddddd')
                .then((res)=>{
                    console.log(res);
                },(err)=>{
                    console.log(err);
                });*/
        },
        components: {
            star
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .detail {
        padding: 40px 0 40px;
    }
    .top {
        display: flex;
        height: 40px;
        background: hotpink;
        color: #FFF;
        position: fixed;
        width: 100%;
        top: 0;
    }
    .top span {
        width: 40px;
        font-size: 27px;
        text-align: center;
        line-height: 40px;
    }
    .top h2 {
        flex: 1;
        font-size: 20px;
        text-align: center;
        line-height: 40px;
    }
    .poster img {
        margin:20px;
    }
    .info {
        display: flex;
        background: #b4b1b1;
        padding: 10px;
    }
    .m-info {
        margin-top: 10px;
        font-size: 14px;
        margin-left: 10px;
    }
    .wtach {
        display: flex;
        color: #FFF;
    }
    .wtach span {
        flex: 1;
        text-align: center;
        line-height: 30px;
        margin: 10px 30px;
        background: hotpink;
        border-radius: 7px;
    }
    .desc {
        line-height: 20px;
        font-size: 14px;
        text-indent: 30px;
        padding: 0 20px;
    }
    .stars p,.comments h3 {
        font-weight: bold;
        font-size: 17px;
        margin-top: 10px;
        margin-left: 20px;
    }
    .stars ul {
        overflow-x: scroll;
        margin: 10px 20px;
    }
    .stars li {
        float: left;
        width: 25%;
        text-align: center;
    }
    .comments h3 p {
        font-size: 12px;
        color: cadetblue;
        float: right;
        margin-right: 20px;
        line-height: 25px;
    }
    .comments li {
        margin: 10px 20px;
        border-top: 1px solid #ccc;
        padding-top: 10px;
    }
    .foot p {
        line-height: 50px;
        font-size: 14px;
        color: cadetblue;
        margin-left: 20px;
        border-top: 1px solid #ccc;
    }
    .pop {
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;
    }
    .pop-left {
        text-align: center;
    }
    .pop-right {
        margin-top: 10px;
    }
</style>


