<template>
  <div class="footBar">
      <router-link to="/home/showing" class="iconfont icon-shouyeshouye" tag="i" active-class="f-active"></router-link>
      <router-link to="/home/movie" class="iconfont icon-dianying" tag="i" active-class="f-active"></router-link>
      <router-link to="/home/find" class="iconfont icon-faxian" tag="i" active-class="f-active"></router-link>
      <router-link to="/home/aboutme" class="iconfont icon-iconfontwohover" tag="i" active-class="f-active"></router-link>
  </div>
</template>

<script>
    import {mapActions} from 'vuex';
    export default {
        name: 'footBar',
        methods: mapActions(
        ['hideNav','showNav']
      )
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import url('../assets/iconfont.css');
    .footBar {
        position: fixed;
        bottom: 0;
        height: 40px;
        line-height: 40px;
        display: flex;
        width: 100%;
        background: hotpink;
    }
    .footBar i {
        flex: 1;
        text-align: center;
        font-size:20px;
        color: #FFF;
    }
    .footBar .icon-dianying {
        font-size: 28px;
        margin-top: 3px;
    }
    .footBar .f-active {
        background-color: deeppink;
        text-shadow: 0 0 3px deepskyblue;
    }
</style>


