import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

import home from '../components/showIng'
import coming from '../components/comImg'
import movie from '../components/movie'
import detail from '../components/detail'
import comment from '../components/comment'
import short from '../components/short'
import stars from '../components/stars'
import search from '../components/search'
import result from '../components/searchResult'

export default new Router({
  mode:'history',
  routes: [
    {path: '/',redirect:'/home'},
    {path:'/home',component:home},
    {path: '/home/showing',component:home},
    {path: '/home/coming',component:coming},
    {path: '/home/coming/:id',component:detail},
    {path: '/home/movie',component: movie},
    {path: '/home/showing/:id',component: detail},
    {path: '/home/movie/:id',component: detail},
    {path: '/detail/:id',component: short},
    {path: '/short/:id',component: comment},
    {path: '/person/:id',component: stars},
    {path: '/home/search',component: search},
    {path: '/home/search/result',component: result}
  ]
})



