<template>
  <div class="header">
      <div class="headerTop">
        <div class="logo">
            <img src="../assets/logo.png" alt="cos-movie" />
        </div>
        <div class="des">
            <h3>找电影</h3>
            <p>查影讯，上找电影就够了！</p>
        </div>
        <div class="search">
            <input type="text" placeholder="搜索电影"  @focus="sub"/>
        </div>
    </div>
    <div class="navbar">
        <router-link to="/home/showing" active-class="active">正在热映</router-link>
        <router-link to="/home/coming" active-class="active">即将上映</router-link>
    </div>
  </div>
</template>

<script>
    export default {
        name: 'header',
        methods: {
            sub: function(){
                this.$router.push({path: '/home/search'});
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .header {
        position: fixed;
        top: 0;
        width: 100%;
        background: #FFF;
    }
    .headerTop {
        display: flex;
        justify-content: space-around;
        padding: 10px 20px;
    }
    img {
        height: 42px;
        width: 42px;
        vertical-align: bottom;
    }
    .search input {
        width: 100px;
        font-size: 14px;
        border-radius: 15px;
        border: none;
        padding-left: 17px;
        background: #eee;
        outline: none;
        height: 40px;
    }
    .search input:focus {
        box-shadow: 0 0 7px lightblue;
    }
    .des h3 {
        font-size: 17px;
        font-weight: 700;
        color: #333;
    }
    .des p {
        font-size: 12px;
        color: #999;
    }
    .navbar {
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        background-color: hotpink;
    }
    .navbar a {
        flex: 1;
        text-align: center;
        text-decoration: none;
        height: 35px;
        line-height: 35px;
        font-size: 20px;
        color: #F9FAFC;
    }
    .navbar .active {
        background-color: deeppink;
        text-shadow: 0 0 3px deepskyblue;
    }
</style>


