<template>
  <div class="showIng">
      <div class="top">
        <span @click="go(-1)"><</span>
        <h2>{{result.title}}</h2>
      </div>
      <p class="re">共{{result.total}}条结果</p>
      <ul>
          <router-link v-for="movie in result.subjects" :to="'/home/showing/'+movie.id" tag="li" :key="movie.id">
              <div class="poster">
                <img :src="movie.images.small" :alt="movie.title" />
              </div>
              <div class="movieInfo">
                  <h2>{{movie.title}}</h2>
                  <star :score="movie.rating.average"></star>
                  <p>{{movie.rating.average}} 分</p>
                  <p>导演：{{movie.directors[0].name}}</p>
                  <p>主演：{{movie.casts[0].name}} ，{{movie.casts[1].name}}</p>
              </div>
          </router-link>
      </ul>
  </div>
</template>

<script>
    import star from './star/star'
    export default {
        name: 'detail',
        data: function(){
            return {
                result: {}
            }
        },
        methods: {
            go: function(n){
                this.$router.go(n);
            }
        },
        mounted () {
            this.$store.dispatch('showLoading');
            this.$store.dispatch('hideFoot');
            this.$store.dispatch('hideNav');
            this.$http.jsonp('https://api.douban.com/v2/movie/search?q=' + this.$route.query.name /*+ '?apikey=0b2bdeda43b5688921839c8ecb20399b&city=%E5%8C%97%E4%BA%AC&client=something&udid=dddddddddddddddddddddd'*/)
                .then((res)=>{
                    
                    this.$store.dispatch('hideLoading');
                    this.result = res.body;
                    // console.log(this.result);
                },(err)=>{
                    console.log(err);
                });
            /*this.$http.jsonp('https://api.douban.com/v2/movie/subject/' + this.$route.params.id + '/reviews?apikey=0b2bdeda43b5688921839c8ecb20399b&start=0&count=20&client=something&udid=dddddddddddddddddddddd')
                .then((res)=>{
                    console.log(res);
                },(err)=>{
                    console.log(err);
                });*/
        },
        components: {
            star
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .re {
        text-align: center;
        font-size: 17px;
        line-height: 40px;
        background: peachpuff;
    }
    .top {
        display: flex;
        height: 40px;
        background: hotpink;
        color: #FFF;
        position: fixed;
        width: 100%;
        top: 0;
    }
    .top span {
        width: 40px;
        font-size: 27px;
        text-align: center;
        line-height: 40px;
    }
    .top h2 {
        flex: 1;
        font-size: 20px;
        text-align: center;
        line-height: 40px;
    }
    .showIng {
        height: 100%;
        overflow: auto;
        width: 100%;
        padding-top: 40px;
    }
    .showIng li {
        display: flex;
        background: #f8f8f8;
        padding: 10px 20px;
        cursor: pointer;
        border-bottom: 1px solid #d6d6d6;
    }
    .showIng li p {
        font-size: 14px;
        color: #666;
    }
    .movieInfo {
        padding-left: 20px;
        vertical-align: top;
    }
    .movieInfo h2 {
        font-size: 20px;
        font-weight: 500;
    }

    img {
        margin-top: 7px;
    }
</style>


